'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { payrollAPI } from '@/lib/api';
import { PayrollRecord } from '@/lib/types';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { DollarSign, Plus, CheckCircle } from 'lucide-react';

export default function PayrollPage() {
  const { user } = useAuth();
  const { toast } = useToast();
  const [payrolls, setPayrolls] = useState<PayrollRecord[]>([]);
  const [loading, setLoading] = useState(true);
  const [openDialog, setOpenDialog] = useState(false);

  const isHRStaff = user?.role === 'hr_staff';
  const canManagePayroll = ['hr_staff', 'admin'].includes(user?.role || '');

  useEffect(() => {
    fetchPayrolls();
  }, []);

  const fetchPayrolls = async () => {
    try {
      const response = await payrollAPI.getAll();
      setPayrolls(response.data.data || []);
    } catch (error: any) {
      toast({
        title: 'Error',
        description: error.response?.data?.message || 'Failed to fetch payroll records',
        variant: 'destructive',
      });
    } finally {
      setLoading(false);
    }
  };

  const handleBulkGenerate = async () => {
    const now = new Date();
    try {
      await payrollAPI.bulkGenerate({
        month: now.getMonth() + 1,
        year: now.getFullYear(),
      });
      toast({
        title: 'Success',
        description: 'Payroll generated for all employees',
      });
      fetchPayrolls();
    } catch (error: any) {
      toast({
        title: 'Error',
        description: error.response?.data?.message || 'Failed to generate payroll',
        variant: 'destructive',
      });
    }
  };

  const handleMarkAsPaid = async (payrollId: string) => {
    try {
      await payrollAPI.update(payrollId, { status: 'paid' });
      toast({
        title: 'Success',
        description: 'Payroll marked as paid successfully',
      });
      fetchPayrolls();
    } catch (error: any) {
      toast({
        title: 'Error',
        description: error.response?.data?.message || 'Failed to update payroll status',
        variant: 'destructive',
      });
    }
  };

  const getStatusBadge = (status: string) => {
    const variants: any = {
      pending: 'default',
      processed: 'secondary',
      paid: 'success',
    };
    return <Badge variant={variants[status] || 'default'}>{status}</Badge>;
  };

  const getEmployeeName = (employeeId: any) => {
    if (!employeeId) return 'Unknown';
    if (typeof employeeId === 'string') return 'Employee';
    return employeeId.name || 'Unknown';
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-96">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto"></div>
          <p className="mt-4 text-muted-foreground">Loading payroll...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold">Payroll Management</h1>
          <p className="text-muted-foreground">
            View and manage employee payroll
          </p>
        </div>
        {canManagePayroll && (
          <Button onClick={handleBulkGenerate}>
            <DollarSign className="mr-2 h-4 w-4" />
            Generate Monthly Payroll
          </Button>
        )}
      </div>

      {/* Payroll Stats */}
      {canManagePayroll && payrolls.length > 0 && (
        <div className="grid gap-4 md:grid-cols-4">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Total Payroll</CardTitle>
              <DollarSign className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                ${payrolls.reduce((sum, p) => sum + p.netSalary, 0).toFixed(2)}
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Paid Records</CardTitle>
              <CheckCircle className="h-4 w-4 text-green-500" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">
                {payrolls.filter(p => p.status === 'paid').length}
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Pending Records</CardTitle>
              <DollarSign className="h-4 w-4 text-orange-500" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-orange-600">
                {payrolls.filter(p => p.status === 'pending').length}
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Processed Records</CardTitle>
              <DollarSign className="h-4 w-4 text-blue-500" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-blue-600">
                {payrolls.filter(p => p.status === 'processed').length}
              </div>
            </CardContent>
          </Card>
        </div>
      )}

      <Card>
        <CardHeader>
          <CardTitle>Payroll Records</CardTitle>
        </CardHeader>
        <CardContent>
          {payrolls.length === 0 ? (
            <div className="text-center py-12">
              <DollarSign className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
              <h3 className="text-lg font-semibold mb-2">No payroll records</h3>
              <p className="text-muted-foreground mb-4">
                {canManagePayroll 
                  ? 'Generate payroll for this month to get started'
                  : 'No payroll records available yet'}
              </p>
              {canManagePayroll && (
                <Button onClick={handleBulkGenerate}>
                  <Plus className="mr-2 h-4 w-4" />
                  Generate Payroll
                </Button>
              )}
            </div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  {user?.role !== 'employee' && <TableHead>Employee</TableHead>}
                  <TableHead>Period</TableHead>
                  <TableHead>Base Salary</TableHead>
                  <TableHead>Bonuses</TableHead>
                  <TableHead>Deductions</TableHead>
                  <TableHead>Net Salary</TableHead>
                  <TableHead>Status</TableHead>
                  {isHRStaff && <TableHead>Actions</TableHead>}
                </TableRow>
              </TableHeader>
              <TableBody>
                {payrolls.map((payroll) => (
                  <TableRow key={payroll._id}>
                    {user?.role !== 'employee' && (
                      <TableCell>
                        {getEmployeeName(payroll.employeeId)}
                      </TableCell>
                    )}
                    <TableCell>
                      {new Date(payroll.year, payroll.month - 1).toLocaleDateString(
                        'en-US',
                        { month: 'long', year: 'numeric' }
                      )}
                    </TableCell>
                    <TableCell>${payroll.baseSalary.toFixed(2)}</TableCell>
                    <TableCell className="text-green-600">
                      +${payroll.bonuses.toFixed(2)}
                    </TableCell>
                    <TableCell className="text-red-600">
                      -${payroll.deductions.toFixed(2)}
                    </TableCell>
                    <TableCell className="font-semibold">
                      ${payroll.netSalary.toFixed(2)}
                    </TableCell>
                    <TableCell>{getStatusBadge(payroll.status)}</TableCell>
                    {isHRStaff && (
                      <TableCell>
                        {payroll.status !== 'paid' ? (
                          <Button
                            size="sm"
                            variant="outline"
                            onClick={() => handleMarkAsPaid(payroll._id)}
                          >
                            <CheckCircle className="h-4 w-4 mr-1" />
                            Mark as Paid
                          </Button>
                        ) : (
                          <span className="text-sm text-muted-foreground">Completed</span>
                        )}
                      </TableCell>
                    )}
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
